.ChartHead {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  height: 100%;
  width: 100%;
}

.ChartHead .TopFret {
  position: absolute;
  background-color: #ddb;
  bottom: 0%;
  width: 100%;
  height: 10%;
  min-height: 1px;
}

.ChartHeadItem {
  flex: 1;
}

.ChartHeadItem svg {
  display: block;
}
