@import "../../globalStyles/colors.css";
@import "../../globalStyles/zIndex.css";

:root {
  --barHeight: 45%;
}

.CellRows {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: space-around;
}

.CellRow {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex: 1;
}

.ChartCell {
  position: relative;
  flex: 1;
}

.ChartCell .Fret {
  position: absolute;
  background-color: #888;
  bottom: 0%;
  width: 100%;
  height: 3%;
  min-height: 1px;
  z-index: var(--zlayer0);
}

.ChartCell .String {
  height: 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  background-color: white;
  min-width: 2px;
  width: 15%;
  z-index: var(--zlayer1);
}

.ChartCell .Note {
  /* padding-bottom and height 0 is a
 CSS Trick to make a scalable square: https://pagecrafter.com/maintain-aspect-ratio-for-html-element-using-only-css-in-a-responsive-design/*/
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--yellow1);
  width: 90%;
  margin: auto;
  padding: 45% 0;
  height: 0;
  border-radius: 50%;
  z-index: var(--zlayer4);
}

.ChartCell .BarSegment {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--yellow1);
  height: var(--barHeight);
  width: 100%;
}

.ChartCell .BarStart {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--yellow1);
  height: var(--barHeight);
  width: 100%;
  border-top-left-radius: 1000px; /* max it out */
  border-bottom-left-radius: 1000px; /* max it out */
}

.ChartCell .BarEnd {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--yellow1);
  height: var(--barHeight);
  width: 100%;
  border-top-right-radius: 1000px; /* max it out */
  border-bottom-right-radius: 1000px; /* max it out */
}

.ChartCell .Contents {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: var(--zlayer2);
}
