@import url(https://fonts.googleapis.com/css?family=Noto+Serif:400,400i,700,700i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --darkAccent1: rgba(65, 106, 147, 1);
  --lightAccent1: rgba(255, 135, 135, 1);
  --light1: rgba(173, 255, 255, 1);
  --strongHighlight1: rgba(0, 255, 246, 1);
  --darkBackground1: rgb(49, 49, 49);
  --yellow1: rgb(255, 255, 0);
  --lightText1: rgba(255, 255, 255, 1);
}

:root {
  --headerHeight: 10vmin;
}

* {
  box-sizing: border-box;
}

body {
  background-color: var(--darkBackground1);
  margin: 10px;
}

.App {
  text-align: center;
}

.App-header {
  position: fixed;
  top: 0;
  width: 100%;
  min-height: 10vmin;
  min-height: var(--headerHeight);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--lightText1);
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.5);
}

.App-body {
  font-size: calc(40px + 2vmin);
  /* margin-top: var(--headerHeight); */
  /* padding-top: 20px; */
  min-height: 100vh;
  color: var(--lightText1);
}

.App-link {
  color: var(--strongHighlight1);
}

.ChordBrowser {
}

.ActiveChord {
  margin-bottom: 50px;
}

.ChordList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 5px;
}
.ChordBrowser .TextInput {
  font-size: 50%;
  margin-bottom: 20px;
}

:root {
  --baseChordWidthPx: 80px;
  --baseChordHeightPx: 140px;
  --baseChordWidthVmin: 8vmin;
  --baseChordHeightVmin: 14vmin;
  --chordMediumScalar: 2;
  --chordLargeScalar: 4;
}

.ChordPresenter {
  padding: 0 3vw;
}

.ChordBox {
  position: relative;
  margin: 0px auto;
}

.scale-small {
  min-width: 80px;
  min-width: var(--baseChordWidthPx);
  min-height: 140px;
  min-height: var(--baseChordHeightPx);
  width: 8vmin;
  width: var(--baseChordWidthVmin);
  height: 14vmin;
  height: var(--baseChordHeightVmin);
}

.scale-medium {
  /* min-width: 100px;
  min-height: 300px;
  width: 10vmin;
  height: 30vmin; */
  min-width: calc(2 * 80px);
  min-width: calc(2 * var(--baseChordWidthPx));
  min-height: calc(2 * 140px);
  min-height: calc(2 * var(--baseChordHeightPx));
  width: calc(2 * 8vmin);
  width: calc(2 * var(--baseChordWidthVmin));
  height: calc(2 * 14vmin);
  height: calc(2 * var(--baseChordHeightVmin));
}

.scale-large {
  /* min-width: 200px;
  min-height: 450px;
  width: 20vmin;
  height: 45vmin; */
  min-width: calc(4 * 80px);
  min-width: calc(4 * var(--baseChordWidthPx));
  min-height: calc(4 * 140px);
  min-height: calc(4 * var(--baseChordHeightPx));
  width: calc(4 * 8vmin);
  width: calc(4 * var(--baseChordWidthVmin));
  height: calc(4 * 14vmin);
  height: calc(4 * var(--baseChordHeightVmin));
}

.Chord {
  width: 100%;
  height: 100%;
}

.ChordTitle {
  font-family: "Noto Serif", serif;
  height: 18%;
}

.ChordChart {
  height: 82%;
}

.Chart {
  width: 100%;
  height: 100%;
}

.ChartHeadContainer {
  height: 14%;
}

.ChartCellsContainer {
  height: 86%;
}

:root {
  --zlayer0: 0;
  --zlayer1: 10;
  --zlayer2: 100;
  --zlayer3: 1000;
  --zlayer4: 10000;
}

:root {
  --barHeight: 45%;
}

.CellRows {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: space-around;
}

.CellRow {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex: 1 1;
}

.ChartCell {
  position: relative;
  flex: 1 1;
}

.ChartCell .Fret {
  position: absolute;
  background-color: #888;
  bottom: 0%;
  width: 100%;
  height: 3%;
  min-height: 1px;
  z-index: var(--zlayer0);
}

.ChartCell .String {
  height: 100%;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background-color: white;
  min-width: 2px;
  width: 15%;
  z-index: var(--zlayer1);
}

.ChartCell .Note {
  /* padding-bottom and height 0 is a
 CSS Trick to make a scalable square: https://pagecrafter.com/maintain-aspect-ratio-for-html-element-using-only-css-in-a-responsive-design/*/
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-color: var(--yellow1);
  width: 90%;
  margin: auto;
  padding: 45% 0;
  height: 0;
  border-radius: 50%;
  z-index: var(--zlayer4);
}

.ChartCell .BarSegment {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-color: var(--yellow1);
  height: 45%;
  height: var(--barHeight);
  width: 100%;
}

.ChartCell .BarStart {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-color: var(--yellow1);
  height: 45%;
  height: var(--barHeight);
  width: 100%;
  border-top-left-radius: 1000px; /* max it out */
  border-bottom-left-radius: 1000px; /* max it out */
}

.ChartCell .BarEnd {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-color: var(--yellow1);
  height: 45%;
  height: var(--barHeight);
  width: 100%;
  border-top-right-radius: 1000px; /* max it out */
  border-bottom-right-radius: 1000px; /* max it out */
}

.ChartCell .Contents {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: var(--zlayer2);
}

.ChartHead {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  height: 100%;
  width: 100%;
}

.ChartHead .TopFret {
  position: absolute;
  background-color: #ddb;
  bottom: 0%;
  width: 100%;
  height: 10%;
  min-height: 1px;
}

.ChartHeadItem {
  flex: 1 1;
}

.ChartHeadItem svg {
  display: block;
}

.TextInput {
  outline: none;
  background-color: transparent;
  color: var(--lightText1);
  border: none;
  border-bottom: 2px solid #888;
  padding: 5px;
}

.TextInput:focus {
  border-bottom-color: var(--yellow1);
  background-color: #444;
}

:root {
  --navHeight: 5vmin;
}

.TopNav {
  height: 5vmin;
  height: var(--navHeight);
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.TopNav a {
  line-height: 5vmin;
  line-height: var(--navHeight);
  font-size: 22px;
}

.TopNav a {
  flex: 1 1;
}

.TopNav a,
.TopNav a:visited,
.TopNav a:link,
.TopNav a:-webkit-any-link {
  color: #999;
  text-decoration: none;
}

.TopNav a.active {
  color: var(--yellow1);
  background-color: #555;
}

.TopNav a:hover {
  color: var(--yellow1);
}

