:root {
  --navHeight: 5vmin;
}

.TopNav {
  height: var(--navHeight);
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.TopNav a {
  line-height: var(--navHeight);
  font-size: 22px;
}

.TopNav a {
  flex: 1;
}

.TopNav a,
.TopNav a:visited,
.TopNav a:link,
.TopNav a:-webkit-any-link {
  color: #999;
  text-decoration: none;
}

.TopNav a.active {
  color: var(--yellow1);
  background-color: #555;
}

.TopNav a:hover {
  color: var(--yellow1);
}
